var Tile = require('./tile.js');

var Board = function() {
  this.grid = [];

  for (var i = 0; i < 10; i++) {
    for (var j = 0; j < 10; j++) {
      var sub = [];

      sub.push(new Tile());
    }

    this.grid.push(sub);
  }

  console.log(this.grid);
};



